.card {
  background: url("https://il6.picdn.net/shutterstock/videos/12036866/thumb/1.jpg");
  border-radius: 2px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 325px;
  text-align: left;
}

img {
  margin-top: 20px;
  width: 200px;
  height: 120px;
  margin-top: 70px;
}

.card {
  margin-left: 20%;
}
.card > .img-container {
  height: 60%;
  overflow: hidden;
  text-align: center;
  background: url("https://il6.picdn.net/shutterstock/videos/12036866/thumb/1.jpg")
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

nav {
    width: 100%;
    font-size: 3rem;
    color: #fff;
    margin-bottom: 50px;
    background-color: rgb(56, 56, 56);
    height: auto;
    font-family: 'Bungee', cursive;
  }
  
  a {
    color: #fed420;
  }
  
  a:hover {
    color: #fed420;
    text-decoration: none;
  }
  
  nav li {
    display: inline-block;
    padding: 26px 0;
    width: 25%;
    /* text-align: center; */
  }
  

  

.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
  background-color: #3165ff;

}

.title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  text-decoration: underline;
  color: #2A77AE;
  margin-bottom: 50px;
}

.card {
    margin-left: 20%;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

